.card {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
  border-radius: 28px;
  background-color: #ffffff;
}

.titleForm {
  font-size: 16px;
  color: #544ca6;
  font-weight: 700;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 14px;
  color: #323759;
}

.checkBox {
  display: grid;
  grid-template-columns: 49% 49%;
  justify-content: space-between;
}

.checkBox [class*="ant-checkbox-inner"] {
  border: 1.8px solid #323759;
  height: 22px;
  width: 22px;
  border-radius: 3px;
}

.checkBox [class*="ant-checkbox-checked"] [class*="ant-checkbox-inner"] {
  background-color: #ffeab7;
  border-color: transparent;
}

.checkBox [class*="ant-checkbox-checked"] [class*="ant-checkbox-inner"]::after {
  border-color: #323759;
  border-width: 4px;
  height: 55%;
  width: 60%;
}

.formUploadImage {
  display: grid;
  grid-template-columns: 32.8% 32.8% 32.8%;
  justify-content: space-between;
  row-gap: 5px;
}

.linkVideo{
  border: 2px dashed #d9d9d9;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.blockHolder {
  background-color: #eae9f2;
}

.blockHolder,
.blockHolder [class*="ant-upload"] {
  height: 125px;
  border-width: 2px;
  width: 100%;
  border-radius: 8px;
  position: relative;
}

img.blockHolder {
  object-fit: contain;
  object-position: center;
}

.blockHolder span[class*="ant-upload"] {
  padding: 0 !important;
}

.actionIcon {
  background: #fbfbfb;
  display: grid;
  place-items: center;
  border-color: transparent;
  margin-bottom: 5px;
  box-shadow: 0.739822px 0.739822px 2.21947px rgba(0, 0, 0, 0.15);
}

.actionRemove{
  position: absolute;
  display: grid;
  place-items: center;
  top: 8px;
  transition: all .3s ease;
  right: 8px;
  padding: 8px ;
  z-index: 3;
  border-radius: 50%;
  border: none;
  color: #ffffff;
  background-color: rgba(163, 163, 164, .8);
}
.actionRemove:hover{
  background-color: #ffffff;
  color: #323759;
}

.blockHolder:hover .mask {
  opacity: 1;
}

.mask{
  opacity: 0;
  display: grid;
  place-items: center;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 2;
  top:0;
  background-color: rgba(88, 84, 107, .5);
}

.selector div[class *= "ant-select-selector"]{
  border-radius: 5px !important;
  background-color: #ffffff;
}