.editBody {
  margin-top: 2rem;
}
.editBody > div[class*="col"] {
  border-top: 1px solid rgba(50, 55, 89, 0.1);
  padding: 15px;
}

.editBody > div[class*="col"]:nth-child(odd) {
  padding-right: 20px !important;
}

.editBody > div[class*="col"]:nth-child(even) {
  border-left: 1px solid rgba(50, 55, 89, 0.1);
  padding-left: 20px !important;
}

.editBodyImage > div[class*="col"] {
  border-top: 1px solid rgba(50, 55, 89, 0.1);
  padding-top: 15px;
}

.twoCols {
  display: grid;
  grid-template-columns: 49% 49%;
  row-gap: 0.5rem;
  justify-content: space-between;
}

.formRow {
  display: grid;
  grid-template-columns: 39% 60%;
  justify-content: space-between;
  align-items: center;
}

.formRow span[class*="ant-input-affix-wrapper"],
.formRow input[class*="ant-input"] {
  border: none;
  border-bottom: 1px dashed #9ea0a5;
}

.formRow span[class*="ant-input-affix-wrapper"] > input {
  border: none;
}

.noteInput textarea {
  padding: 0;
}

.contactInfo {
  background: #fbfbfe;
  border: 1px solid #eae9f2;
  box-sizing: border-box;
  padding: 15px;
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.04);
  border-radius: 18px;
}

.inputBorderBottom [class*="ant-input"] {
  border: none;
  border-bottom: 1px solid #222222 !important;
  background-color: transparent;
}

.noteStick {
  width: 55%;
  position: absolute;
  left: 25%;
  height: 180px;
  border-radius: 25px 25px 0 0;
  z-index: 3;
  transition: all .3s step-end;
  background: #fbfbfe;
  border: 1px solid #a4aab8;
}

.noteStick button{
  float: right;
  top:-50px;
  right: 10px;
}

.noteContent{
  overflow: auto;
  height: 100%;
}

.noteContent::-webkit-scrollbar{
  width: 5px;
}

.noteContent::-webkit-scrollbar-thumb{
  background-color: #FDD84C;
  border-radius: 10px;
}
