.titleEdit {
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 30px;
  letter-spacing: 1px;
  color: #333333;
}

.editHeader {
  height: 7rem;
}
.editHeader > div[class*="ant-col"]:not(:last-child) {
  border-right: 1px solid #fff;
  padding-right: 1rem;
}

.editHeader > div[class*="ant-col"]:not(:first-child) {
  padding-left: 1rem;
}

.editHeader > div[class*="ant-col"] {
  flex-shrink: initial;
  align-items: center;
}

.btnCancel {
  color: #fff;
  background-color: rgba(241, 104, 86, 1);
}

.btnSave {
  color: #fff;
  background-color: #544ca6;
}

.btnSubmit {
  color: #fff;
  background-color: rgba(96, 210, 141, 1);
}

.tabsBar div[class*="ant-tabs-tab-btn"] {
  color: rgba(50, 55, 89, 0.7);
}

.tabsBar div[class*="ant-tabs-tab-btn"][aria-selected="true"],
.tabsBar div[class*="ant-tabs-tab-btn"]:hover {
  color: #544ca6;
}

.tabsBar div[class*="ant-tabs-ink-bar"] {
  background-color: #544ca6;
  height: 4px !important;
  border-radius: 5px;
}

.inputBorderBottom [class*="ant-input"] {
  border: none;
  border-bottom: 1px solid #222222;
  background-color: transparent;
}

.actionConfirm:nth-child(1) {
  background: rgba(164, 170, 184, 0.3);
}
.actionConfirm {
  border-radius: 10px;
  color: #222222;
  font-weight: 500;
  background: #fdd84c;
  box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.05);
  border-radius: 12px;
  border: none;
}

.confirmModal div[class*="ant-modal-content"] {
  border-radius: 15px;
  box-shadow: 4px 4px 30px rgba(0, 0, 0, 0.15);
}
